summary::after{
    transition: all 400ms;
    transform: rotate(0);
}

details[open] summary::after {
    transform: rotate(45deg);
}

@keyframes slideDown {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
details[open] {
    border-bottom: 4px solid #E756BE;
}
  

details[open]>p{
    animation-name: slideDown;
    animation-duration: 400ms;
    animation-timing-function:ease-in;
}